@import "./const.scss";

.cell-action.action-plus {
    left: calc(1 * $cell-height);
}

.cell-action.measure-positive.action-minus {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-zero.action-empty:not(.today),
.cell-action.measure-empty.action-minus.today,
.cell-action.measure-positive.action-empty,
.cell-action.measure-positive.action-zero,
.cell-action.measure-positive.action-na,
.cell-action.measure-na.action-empty:not(.today),
.cell-action.measure-na.action-minus,
.cell-action.measure-na.action-na,
.cell-action.measure-empty.action-empty,
.cell-action.measure-zero.action-minus,
.cell-action.measure-zero.action-zero
{
    display: none;
}

.cell-action.measure-na.action-empty.today {
    left: calc(-2 * $cell-height);
}

.cell-action.measure-na.action-zero:not(.today) {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-na.action-zero.today {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-empty.action-na.today {
    left: calc(-2 * $cell-height);
}

.cell-action.measure-empty.action-zero.today {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-zero.action-na:not(.today) {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-zero.action-na.today {
    left: calc(-1 * $cell-height);
}

.cell-action.measure-zero.action-empty.today {
    left: calc(-2 * $cell-height);
}

// Универсальные правила позиционирования для всех типов тасок
.cell-action.left-2 {
    left: calc(-2 * $cell-height);
}

.cell-action.left-1 {
    left: calc(-1 * $cell-height);
}

.cell-action.right-1 {
    left: calc(1 * $cell-height);
}

