.top-nav-wrapper {
  button.top-nav-bread {
    background: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
  }

  nav.top-nav {
    position: absolute; // Position the modal fixed within the viewport
    top: 0;
    left: 0;
    min-width: 30%;
    // position: absolute; // Position the nav absolutely
    // top: 0; // Align to the top of the closest positioned ancestor
    // left: 0; // Align to the left of the closest positioned ancestor
    z-index: 1000; // Ensure it's above most other content

    &.active {
      min-width: 20%;
      border: 1px solid lightgray;
      background: white;
      box-shadow: 2px 2px 10px black;
    }

    ul {
      list-style-type: none;
      padding: 0;
      background: transparent; // Optional: set a background color for better visibility
      margin-top: 10px; // Optional: add some space between the button and the list
    }

    ul li a {
      text-decoration: none;
      color: black;
      display: block;
      padding: 8px;
    }
  }

  //   // Example media query for larger screens
