@import "./const.scss";


.task-collapsed,
.task-not-displayed {
  display: none;
}


.task-row {

  .drag-handler {
    width: $drag-width;
    transform: translate(0, 50%);

    .circle-column span.circle {
      background-color: $border-color;
    }
  }

  .arrow-handler {
    width: $drag-width;
  }

}


.task-row {
  display: flex;
  padding: 0;
  margin: 0;
  height: $task-row-height;
  background: white;
  user-select: none;

  .task-title-column {
    display: flex;
    box-sizing: border-box;
  }

  .title {
    height: 100%;
    width: 100%;

    .long-click-input {
      height: 100%;
      width: 100%;

      button, input {
        height: 100%;
        width: 100%;
        background: $title-background;
        padding-left: $title-padding-left;
      }
    }

    .title-static {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      background: $title-background;
      padding-left: $title-padding-left;
      box-sizing: border-box;
    }

    // border-left: $cell-border-size solid $border-color;
  }

  .points {
    height: 100%;
    display: flex;

    input {
      width: 25px;
      height: 100%;
      background: $title-background;
      font-size: 10px;
      text-align: right;
      padding-right: 5px;
      color: $border-color;

      &.zero {
        color: transparent;

        &:hover,
        &:focus {
          color: black;
        }
      }
    }
  }

  .compact-badges {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 2px;
    padding-right: 6px;
    background: $title-background;

    .badge {
      font-size: 9px;
      font-family: monospace;
      padding: 1px 4px;
      border-radius: 3px;
      white-space: nowrap;
      line-height: 1.2;
    }

    .badge-time {
      background: rgba(0, 0, 0, 0.08);
      color: #666;
    }

    .badge-reward {
      &.badge-positive {
        background: rgba(46, 125, 50, 0.15);
        color: #2e7d32;
      }
      
      &.badge-neutral {
        background: rgba(0, 0, 0, 0.08);
        color: #666;
      }

      &.badge-negative {
        background: rgba(198, 40, 40, 0.15);
        color: #c62828;
      }
    }
  }

  .reward-column {
    height: 100%;
    width: $cell-width;
    min-width: $cell-width;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;

    &.reward-header {
      color: $unobtrusive-color;
    }

    .reward-input {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      input {
        height: 100%;
        width: 100%;
        text-align: center;
        border: none;
        outline: none;
        background: $title-background;
        font-size: 10px;
        font-family: monospace;
      }

      div {
        height: 100%;
        width: 100%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 10px;
        font-family: monospace;
        background: $title-background;
      }

      &.reward-positive {
        input, div {
          background: $title-background;
          color: #2e7d32;
        }
      }

      &.reward-negative {
        input, div {
          background: $title-background;
          color: #c62828;
        }
      }

      &.reward-zero {
        input, div {
          background: $title-background;
          color: $task-color-zero;
        }
      }
    }
  }

  .time-column {
    height: 100%;
    width: $cell-width;
    min-width: $cell-width;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;

    &.time-header {
      height: $task-row-height;
      text-align: center;
      border-style: solid;
      border-image-slice: 1;
      border-width: $cell-border-size;
      border-image-source: linear-gradient(to top, $cell-border-color 50%, transparent 100%);
      border-top: none;
      border-left: none;
      border-right: none;
      color: $unobtrusive-color;
      font-weight: normal;
      font-size: 10px;
    }

    .time-input {
      height: 100%;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: center;

      input {
        height: 100%;
        width: 100%;
        text-align: center;
        border: none;
        outline: none;
        background: $title-background;
        font-size: inherit;
        font-family: monospace;
      }

      div {
        height: 100%;
        width: 100%;
        text-align: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: monospace;
        cursor: pointer;
        background: $title-background;
      }
    }
  }

  &.task-time-out-of-range {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: $task-background-failed;
      z-index: 10;
      pointer-events: none;
    }

    > * {
      position: relative;
      z-index: 1;
    }
  }

  &.task-time-in-range {
    position: relative;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: linear-gradient(135deg, $fire-gradient-color-start 0%, $fire-gradient-color-middle 50%, $fire-gradient-color-end 100%);
      background-size: $fire-gradient-size-x $fire-gradient-size-y;
      box-shadow: inset 0 0 $fire-glow-size $fire-glow-color;
      animation: fireGradient $fire-gradient-duration ease infinite;
      z-index: $fire-overlay-z-index;
      pointer-events: none;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: radial-gradient(circle at 50% 50%, $fire-radial-color 0%, transparent $fire-radial-transparent-stop);
      animation: fireFlicker $fire-flicker-duration ease-in-out infinite;
      pointer-events: none;
      z-index: $fire-flicker-z-index;
    }

    > * {
      position: relative;
      z-index: 1;
    }
  }


  .measures {
    height: 100%;
    display: flex;
  }
}

@keyframes fireGradient {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes fireFlicker {
  0%, 100% {
    opacity: $fire-flicker-opacity-min;
    transform: scale(1);
  }
  25% {
    opacity: $fire-flicker-opacity-max;
    transform: scale($fire-flicker-scale-max);
  }
  50% {
    opacity: $fire-flicker-opacity-mid;
    transform: scale($fire-flicker-scale-min);
  }
  75% {
    opacity: $fire-flicker-opacity-max;
    transform: scale($fire-flicker-scale-mid);
  }
}

