.switch {
  width: 50px;
  height: 30px;
  background-color: #ccc;
  border-radius: 15px;
  cursor: pointer;
  position: relative;
  transition: background-color 0.2s;

  &.on {
    background-color: #4cd964;
  }

  .switch-handle {
    width: 28px;
    height: 28px;
    background-color: white;
    border-radius: 50%;
    position: absolute;
    top: 1px;
    left: 1px;
    transition: left 0.2s;

    .switch.on & {
      left: 21px;
    }
  }
}
