.context-menu {
    display: none;
    position: fixed;
    z-index: 1000;
    width: auto;
    background-color: white;
    border: 1px solid #ddd;
    box-shadow: 0px 2px 5px rgba(0,0,0,0.2);
    padding: 10px 0;
  }
  
  .context-menu.show {
    display: block;
  }
  
  .context-menu-item {
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .context-menu-item:hover {
    background-color: #f5f5f5;
  }
