button.custom-button {
    background: none;
    border: none;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    text-align: left;
    text-transform: none;
    text-indent: 0;
    text-shadow: none;
    display: inline;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    box-shadow: none;
    border-radius: 0;
}