@import "./const.scss";

button.cell-action {
    background-color: #eee !important;

    width: $cell-width;
    height: $task-row-height;
    min-width: $cell-width;
    min-height: $task-row-height;

    position: absolute;

    border: $cell-border-size solid black;
    text-align: center;
    width: $cell-width;
    height: $task-row-height;

    &.hover {
        font-weight: bold;
        background-color: #aaa !important;
    }

    // debug {{
    // background-color: rgba(0, 0, 0, 0.25) !important;
    // color: red;
    // font-size: 40px;
    // }} 
}