/* ErrorPopup.css */
.error-popup {
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #fff;
    color: red;
    padding: 20px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 1000;
    width: 80%;
    max-width: 400px;
    box-sizing: border-box;
  }
  
  .close-icon {
    cursor: pointer;
    margin-left: 20px;
  }
  
  .fade-out {
    animation: fadeOut 5s forwards;
  }
  
  @keyframes fadeOut {
    to {
      opacity: 0;
      visibility: hidden;
    }
  }
  