input.custom-input {
    margin: 0;
    padding: 0;
    border: none;
    font-size: 100%;
    /* Resets font-size */
    font: inherit;
    /* Inherits font settings from parent */
    color: inherit;
    /* Inherits text color from parent */
    background-color: transparent;
    /* Removes any default bg */
    width: auto;
    /* Resets width */
    height: auto;
    /* Resets height */
    box-shadow: none;
    /* Removes any shadows */
    outline: none;
    /* Removes focus outline */
    display: inline;
    /* Ensures the input remains inline */
    vertical-align: baseline;
    /* Aligns with baseline of surrounding text */
    box-sizing: content-box;
    /* Resets box-sizing */
    -webkit-appearance: none;
    /* Removes default webkit styles */
    -moz-appearance: none;
    /* Removes default Firefox styles */
}