@import "./const.scss";


.cell {
    width: $cell-width;

    height: 100%;
    box-sizing: border-box;
    border-right: $cell-border-size solid $border-color;
    border-bottom: $cell-border-size solid $border-color;

    input,
    button {
        width: 100%;
        height: 100%;
        text-align: center;
    }

}