@import "./const.scss";

.stat-cell {
    user-select: none;
}

.color-stats {
    .stat-cell {
        button {
            color: $task-background-positive-fulldone;
        }

        &.is-lte-60 button {
            // #991;
            color: #aaa59d;
        }

        &.is-lte-30 button {
            color: $task-background-negative-fulldone;
        }
    }
}

.color-stats.color-stats-except-zeros .stat-cell.is-zero button {
    color: transparent;
}