@import "./const.scss";

.reward-controls-section {
  padding: 10px;
  background: #f5f5f5;
  border-radius: 8px;
}

.reward-controls-title {
  margin: 0 0 10px 0;
}

.reward-controls-row {
  display: flex;
  gap: 10px;
  align-items: flex-end;

  &--wrap {
    flex-wrap: wrap;
  }
}

.reward-controls-field {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.reward-controls-label {
  font-size: 11px;
  color: #666;
  margin: 0;
}

.reward-controls-input {
  padding: 5px 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
  width: 80px;

  &--number {
    width: 60px;
  }
}

.reward-controls-select {
  padding: 5px 10px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.reward-controls-button {
  padding: 5px 15px;
  border-radius: 4px;
  color: white;
  border: none;
  cursor: pointer;

  &--green {
    background: #4CAF50;
  }

  &--blue {
    background: #2196F3;
  }
}

.reward-controls-hint {
  font-size: 12px;
  color: #666;
  margin: 8px 0 0 0;
}

