.death-gauge-overlay {
  display: flex;
  flex-direction: column;
  position: relative;
  left: 0px;
  gap: 10px;

  .gauge-overlay-horizontal {
    display: flex;
    flex-direction: row;
    gap: 10px;
    justify-content: center;
  }

  .gauge-overlay-vertical {
    position: absolute;
    left: 0px;
    display: flex;
    flex-direction: column;
    gap: 0;
    align-items: center;
  }

  .gauge-input-sep {
    display: block;
    min-height: 10px;
    height: 40px;
    width: 40px;
  }

  button.gauge-action {
    text-align: center;
    width: 40px;
    height: 40px;
    border: 1px solid black;
    background-color: white;

    &.hover {
      font-weight: bold;
      background-color: lightgray;
    }

    &.gauge-action-play {
      position: absolute;
      width: 60px;
      left: 40px;
      top: 0px;
    }

    &.gauge-action-edit {
      position: absolute;
      width: 60px;
      left: -40px;
      top: 0px;
    }
  }
}