@import "./const.scss";

.task-table {

    .stat-cell-head-title {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: $cell-border-size solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
    }

    .stat-cell button {
        width: $cell-width;
        height: $cell-height;
        box-sizing: border-box;
        border-bottom: $cell-border-size solid $border-color;
        color: $border-color;
        background: white;
        line-height: $cell-height;
        vertical-align: middle;
        text-align: center;
        font-size: 80%;
        user-select: none;
    }

    .tasks {
        .sortable>:last-child .stat-cell {
            border-bottom: none;
        }
    }

    .table-head {
        display: flex;
        background: white;
        user-select: none;
    }

    .context-menu-is-showing .date-cell {
        background-color: lightblue;
    }


    .date-cell {
        height: $task-row-height;
    }

    .task-title-column {
        min-width: $title-min-width;
        // border-right: $cell-border-size solid $border-color;
        // border-bottom: $cell-border-size solid $border-color;
        box-sizing: border-box;

        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        border-image-source: linear-gradient(to left, $cell-border-color, transparent);

        border-left: none;
        border-top: none;

    }

    .table-head .task-title-column {
        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        // border-image-source: linear-gradient(to top, transparent 0%, $cell-border-color 50%, transparent 100%);
        border-image-source:
            linear-gradient(-15deg, $cell-border-color 0 0, transparent 50% 50%);
        border-top: none;
        border-left: none;

        // background: linear-gradient(-10deg, $cell-border-color 0 0, transparent 50% 50%);
        // border-bottom: none;
    }

    .date-cell {
        // border-right: $cell-border-size solid $border-color;
        // border-bottom: $cell-border-size solid $border-color;
        // background-color: lightgray;
        box-sizing: border-box;
        text-align: center;
        width: $cell-width;
        min-width: $cell-width;

        border-style: solid;
        border-image-slice: 1;
        border-width: $cell-border-size;
        // border-image-source: linear-gradient(to top, transparent 0%, $cell-border-color 50%, transparent 100%);
        border-image-source: linear-gradient(to top, $cell-border-color 50%, transparent 100%);
        border-top: none;
        // border-bottom: none;
        border-left: none;

        color: $cell-content-color;

        &:not(.today) {
            color: $border-color;
        }

        &.time-header {
            color: $unobtrusive-color;
        }

        &.wd0 {
            * {
                font-weight: bolder;
            }
        }
    }

    .days-wrapper {
        .days {
            display: flex;
        }
    }

    .table-footer {
        .task-title-column {
            height: $task-row-height;
            display: flex;

            .margin {
                display: inline-block;
                width: $drag-width;
                box-sizing: border-box;
                height: $task-row-height;
            }

            .title {
                display: inline-block;
                width: calc(100% - $drag-width);
                height: 100%;
                box-sizing: border-box;

                input {
                    width: 100%;
                    height: 100%;
                    padding-left: $title-padding-left;

                    &::placeholder {
                        color: $border-color;
                    }
                }

                border-right: $cell-border-size solid rgba(0, 0, 0, 0.1);
                border-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0)) 1 100%;
            }

            border-right: none;
            border-bottom: none;
        }
    }
}


.stat-cell-head-title {
    position: relative;

    button.custom-button.current-stat-title {
        position: absolute;
        left: 0;
        font-size: 80%;
        width: $cell-width;
        height: $cell-height;
        text-align: center;
    }
}

.change-stat-overlay {
    position: relative;
    left: calc(-2 * $cell-width - 1px);
    width: calc($cell-width * 3);
    display: flex;
    z-index: 1000;

    .action-button {
        min-width: $cell-width;
        width: $cell-width;
        min-height: $cell-height;
        height: $cell-height;
        box-sizing: border-box;
        border-left: 1px solid $border-color;
        border-bottom: 1px solid $border-color;
        background: white;
        font-size: 80%;

        &.hover {
            font-weight: bold;
            background-color: #aaa;
        }
    }
}


.gray-out-done-tasks .task-wrapper.today-measured {
    filter: grayscale(50%);
    opacity: 0.25;
}

.stat-row {
    filter: grayscale(50%);
    opacity: 0.25;
}
