.rectangle {
  width: 100%; /* Adjust based on your preference */
  height: 100%; /* Adjust based on your preference */
  overflow: hidden;
  position: relative;
}

.arrow {
  width: 100%;
  height: 100%;
  animation: moveRight 1s linear infinite;
}

.caption {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 50%; /* Position the top of the caption at the center of the rectangle */
  transform: translateY(-50%); /* Shift the caption up by half its height to center it vertically */
  font-size: 16px;
}

@keyframes moveRight {
  0% {
    transform: translateX(5%);
  }
  50% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(5%);
  }
}
