@import "./const.scss";

$arrow-color: lightgray;
$arrow-size: 6px;

button.arrow-handler-button {
    background: none;
    border: none;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    margin: 0;
    width: auto;
    height: 100%;
    text-align: left;
    text-transform: none;
    text-indent: 0;
    text-shadow: none;
    display: inline-block;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    box-shadow: none;
    border-radius: 0;

    &:hover,
    &:focus {
        background: none;
        border: none;
        box-shadow: none;
    }

    &:active {
        outline: none;
        border: none;
    }

    &::before,
    &::after {
        background: none;
        content: none;
    }

    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }

    &:focus {
        outline: none;
    }
}

.arrow-handler-button {
    .arrow-handler {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        user-select: none;

        .arrow {
            width: 0;
            height: 0;
            border-style: solid;
            border-color: $arrow-color;
            transition: transform 0.2s ease;

            &.arrow-right {
                border-top: $arrow-size solid transparent;
                border-bottom: $arrow-size solid transparent;
                border-left: $arrow-size solid $arrow-color;
                border-right: none;
            }

            &.arrow-down {
                border-left: $arrow-size solid transparent;
                border-right: $arrow-size solid transparent;
                border-top: $arrow-size solid $arrow-color;
                border-bottom: none;
            }
        }
    }
}

