.modal {
  position: fixed; // Position the modal fixed within the viewport
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5); // Semi-transparent backdrop

  .modal-content {
    background-color: #fff; // White background for the modal content
    padding: 20px;
    border-radius: 5px; // Slightly rounded corners for the modal content
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); // A subtle shadow for depth
    width: 90%; // Use a percentage width for responsiveness, up to a max width
    max-width: 500px; // Maximum width of the modal content

    button {
      margin-top: 20px; // Space above the button
      padding: 10px 20px; // Padding inside the button
      border: none; // Remove default border
      border-radius: 5px; // Slightly rounded corners for the buttons
      cursor: pointer; // Pointer cursor on hover

      &:first-of-type {
        margin-right: 10px; // Space between the "OK" and "Cancel" buttons
      }

      &:hover {
        opacity: 0.8; // Slightly fade the button on hover for a visual effect
      }
    }
  }
}


