$cell-border-size: 0.5px;
$cell-border-color: rgba(0, 0, 0, 0.25);
$cell-border-edge-radius: 2px;
$cell-border-edge-type: solid;
$cell-content-color: #222;

$task-background-positive: #e8f5e9;
$task-background-positive-partdone: #c8e6c9;
$task-background-positive-fulldone: #a5d6a7;
$task-background-negative: #ffebee;
$task-background-negative-partdone: #ffcdd2;
$task-background-negative-fulldone: #ef9a9a;
$task-background-resource: #fffde7;
$task-background-resource-partdone: #fff9c4;
$task-background-resource-fulldone: #fff59d;
$task-background-neutral: #fafafa;
$task-background-neutral-partdone: #f5f5f5;
$task-background-neutral-fulldone: #eeeeee;

// there are no empty cells in the past
// they are all appear as zero
$task-background-empty-today: white;

$task-background-zero: rgb(238, 238, 238);
$task-color-zero: gray;
$task-background-failed: rgba(244, 204, 204, 0.4);
$unobtrusive-color: rgba(0, 0, 0, 0.33);

$cell-width: 50px;
$task-row-height: 50px;
$cell-height: $task-row-height;
$drag-width: 20px;
$border-color: $cell-border-color;
$title-background: $task-background-neutral;
$title-padding-left: 2px;
$title-min-width: 170px;

// Fire effect (task-time-in-range)
$fire-gradient-color-start: rgba(255, 243, 205, 0.2);
$fire-gradient-color-middle: rgba(255, 224, 130, 0.4);
$fire-gradient-color-end: rgba(255, 243, 205, 0.1);
$fire-gradient-size-x: 200%;
$fire-gradient-size-y: 200%;
$fire-glow-color: rgba(255, 193, 7, 0.2);
$fire-glow-size: 20px;
$fire-radial-color: rgba(255, 152, 0, 0.3);
$fire-radial-transparent-stop: 70%;
$fire-gradient-duration: 1.0s;
$fire-flicker-duration: 1.0s;
$fire-flicker-opacity-min: 0.1;
$fire-flicker-opacity-mid: 0.5;
$fire-flicker-opacity-max: 0.9;
$fire-flicker-scale-min: 0.90;
$fire-flicker-scale-mid: 1.10;
$fire-flicker-scale-max: 1.10;
$fire-overlay-z-index: 10;
$fire-flicker-z-index: 11;
