$size: 3px;
$color: lightgray;

button.drag-handler-button {
    background: none;
    border: none;
    color: inherit;
    font: inherit;
    line-height: normal;
    overflow: visible;
    padding: 0;
    margin: 0;
    width: auto;
    height: auto;
    text-align: left;
    text-transform: none;
    text-indent: 0;
    text-shadow: none;
    display: inline;
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    box-shadow: none;
    border-radius: 0;

    &:hover,
    &:focus {
        background: none;
        border: none;
        box-shadow: none;
    }

    &:active {
        outline: none;
        border: none;
    }

    &::before,
    &::after {
        background: none;
        content: none;
    }

    // You might also want to reset pseudo-elements
    &::-moz-focus-inner {
        border: 0;
        padding: 0;
    }

    &:focus {
        outline: none;
    }
}

.drag-handler-button {
    .drag-handler {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: grab;
        user-select: none;

        .circle-group {
            display: flex;
            align-items: center;

            .circle-column {
                margin: 1px 1px; // Margin between columns

                .circle {
                    width: $size;
                    height: $size;

                    margin: 100% 0;

                    background-color: $color;
                    border-radius: 50%; // makes it a circle
                    display: block; // makes it stack vertically
                }
            }
        }
    }
}

.sortable-drag-handler.disabled {
    .drag-handler-button {
        opacity: 0;
        cursor: default;

        .drag-handler {
            cursor: default;
        }
    }
}