.swipeable {
    // touch-action: none;
    overflow: hidden;
    height: 100%;
    position: relative;

    .selected {
        font-weight: bold;
    }

    .left-actions {
        display: flex;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
    }

    .right-actions {
        display: flex;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;

    }

    .swipe-content {
        height: 100%;
    }

    .swipeable-right-action {
        height: 100%;
    }
    
    .swipeable-left-action {
        height: 100%;
    }

    &.swipeable-right-on {
        .swipeable-left-action {
            display: none;
        }
    }

    &.swipeable-left-on {
        .swipeable-right-action {
            display: none;
        }
    }

}