.rectangle {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.arrow {
  width: 100%;
  height: 100%;
  animation: moveLeft 1s linear infinite; /* Updated animation name */
}

.caption {
  position: absolute;
  width: 100%;
  text-align: center;
  top: 50%;
  transform: translateY(-50%);
  font-size: 16px;
}

@keyframes moveLeft {
  0% {
    transform: translateX(-5%);
  }
  50% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(-5%);
  }
}
