@import "./const.scss";

.checklist {
  width: 100%;
  background: #fafafa;
  border-top: 1px solid rgba(0, 0, 0, 0.08);

  &.collapsed {
    .checklist-content {
      display: none;
    }
  }

  .checklist-header {
    display: flex;
    align-items: center;
    padding: 6px 12px;
    cursor: pointer;
    user-select: none;
    font-size: 11px;
    color: #666;
    background: #f5f5f5;

    &:hover {
      background: #efefef;
    }

    .checklist-toggle {
      font-size: 8px;
      margin-right: 8px;
      color: #999;
    }

    .checklist-summary {
      font-family: monospace;
    }
  }

  .checklist-content {
    padding: 2px 0;

    .sortable {
      .sortable-item {
        .checklist-item {
          margin: 0;
        }
      }
    }
  }

  .checklist-item {
    display: flex;
    align-items: center;
    padding: 2px 8px;
    min-height: 24px;
    gap: 4px;
    box-sizing: border-box;
    background: #fafafa;

    &:hover {
    }
  }

  .checklist-drag-handle {
    width: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #bbb;
    font-size: 10px;
    user-select: none;

    .drag-dots {
      letter-spacing: -2px;
    }

    &.placeholder {
      opacity: 0.3;
      cursor: default;
    }
  }

  .checklist-checkbox {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 18px;
    height: 18px;
    cursor: pointer;

    input {
      position: absolute;
      opacity: 0;
      width: 0;
      height: 0;
    }

    .checkmark {
      width: 14px;
      height: 14px;
      border: 1.5px solid #ccc;
      border-radius: 3px;
      background: white;
      transition: all 0.15s ease;
      display: flex;
      align-items: center;
      justify-content: center;

      &::after {
        content: '';
        display: none;
        width: 4px;
        height: 8px;
        border: solid white;
        border-width: 0 2px 2px 0;
        transform: rotate(45deg) translate(-1px, -1px);
      }
    }

    input:checked+.checkmark {
      background: #4caf50;
      border-color: #4caf50;

      &::after {
        display: block;
      }
    }

    &.placeholder .checkmark {
      opacity: 0.25;
      border-style: dashed;
    }
  }

  .checklist-text {
    flex: 1;
    font-size: 11px;
    color: #333;
    padding: 2px 6px;
    cursor: text;
    word-break: break-word;

    &.checked {
      text-decoration: line-through;
      color: #999;
    }

    &.placeholder-text {
      color: #bbb;
      font-style: italic;
    }
  }

  .checklist-text-input {
    flex: 1;
    font-size: 11px;
    color: #333;
    padding: 2px 6px;
    border: none;
    outline: none;
    background: rgba(0, 0, 0, 0.03);
    border-radius: 3px;

    &::placeholder {
      color: #bbb;
      font-style: italic;
    }

    &.placeholder-input {
      background: transparent;
    }
  }


  .checklist-placeholder {
    cursor: pointer;
    outline: none;

    &:hover,
    &:focus {
      .placeholder-item {
        background: rgba(0, 0, 0, 0.03);
      }

      .checklist-checkbox.placeholder .checkmark {
        opacity: 0.5;
        border-color: #999;
      }

      .placeholder-text {
        color: #777;
      }
    }

    .placeholder-item {
      padding: 2px 8px;
      min-height: 24px;
      width: 100%;
      display: flex;
      align-items: center;
      gap: 4px;
    }
  }

}