.edit-task-modal {
  .modal-field {
    margin-bottom: 15px;

    label {
      display: block;
      margin-bottom: 5px;
      font-weight: 500;
      color: #333;
    }

    input {
      width: 100%;
      padding: 8px;
      border: 1px solid #ddd;
      border-radius: 4px;
      font-size: 14px;
      box-sizing: border-box;

      &:focus {
        outline: none;
        border-color: #4a90e2;
      }

      &:disabled {
        background-color: #f5f5f5;
        cursor: not-allowed;
      }
    }

    .checkbox-label {
      display: flex;
      align-items: center;
      gap: 5px;
      margin-top: 5px;
      margin-bottom: 0;
      font-weight: normal;
      cursor: pointer;

      input[type="checkbox"] {
        width: auto;
        margin: 0;
        padding: 0;
        border: none;
        border-radius: 0;
        cursor: pointer;
        flex-shrink: 0;
      }

      span {
        user-select: none;
      }
    }
  }

  .reward-field {
    display: flex;
    align-items: center;
    gap: 10px;

    .checkbox-label {
      display: flex;
      align-items: center;
      gap: 5px;
      margin-bottom: 0;
      font-weight: normal;
      cursor: pointer;

      input[type="checkbox"] {
        width: auto;
        margin: 0;
        cursor: pointer;
      }

      span {
        user-select: none;
      }
    }

    input[type="number"] {
      flex: 1;
    }
  }

  .modal-buttons {
    display: flex;
    justify-content: flex-end;
    gap: 10px;
    margin-top: 20px;
  }
}

