.accordion-container {
  overflow: hidden;
  position: relative;
  width: 100%;
  height: 100%; /* Adjust as needed */
}

.accordion-content {
  display: flex;
  transition: transform 0.3s ease;
}

.accordion-item {
  min-width: 100%;
  height: 100%; /* Adjust as needed */
  box-sizing: border-box;
}
